$apple-green: #62a03f;
$green-haze: #009d77;
$japanese-laurel: #008000;
$persian-green: #00a0a7;
$geyser-blue: #d8dde7;
$dodger-blue: #1391ff;
$endeavour-blue: #005dae;
$scampi-purple: #55a;
$electric-violet: #7300e5;
$persian-red: #cf3030;
$mango-tango: #e97500;

$white-gray: #c6c6c6;
$light-gray: #2b2835;
$medium-gray: #201e28;
$dark-gray: #141319;

$brandmaker-orange: #ea7500;

$darkmode-bg-brighter: #201e28;
$darkmode-bg-brightest: #2b2835;

// Theme

$bg-dark: #0c0f2c;
$bg-light: #191b30;
$bg-lighter-blue: #474a64;
$bg-light-blue: #333755;
$bg-lighter: #232539;
$bg-lightest: #30334b;
$bg-darker: #0a0c23;

$bg-medium: #232639;

$color-primary: #89bf04 !default;
$color-secondary: #9012fe !default;
$color-info: #4990e2 !default;
$color-warning: #ff6060 !default;
$color-danger: #f00 !default;

$color-primary-hover: lighten($color-primary, 0.5%) !default;

$_color-post: #38a574 !default;
$_color-get: #4194e7 !default;
$_color-put: #fca130 !default;
$_color-delete: #eb2727 !default;
$_color-head: #9012fe !default;
$_color-patch: #13bb96 !default;
$_color-disabled: #828391 !default;
$_color-options: #0d5aa7 !default;

// Response Codes

$_color-informational: #cfba44;
$_color-successful: #6cb617;
$_color-redirects: #cc6917;
$_color-client-error: #810303;
$_color-server-error: #810303;
$_color-not-defined: #686868b6;

// Edit (Text Area/Parameters)

$edit-border: $brandmaker-orange;
$edit-background: $dark-gray;
$edit-scroll: $light-gray;

// Authorize

$auth-container-border-color: $bg-light-blue !default;
$auth-input-background: $light-gray !default;
$auth-input-border: rgba(255, 255, 255, 0.5) !default;
$auth-input-border-select: $brandmaker-orange !default;
$auth-input-border-error: $color-danger !default;
$auth-input-placeholder: $white-gray !default;
$auth-input-placeholder-error: $white-gray !default;
$auth-input-text: $white !default;
$auth-select-all-none-link-font-color: $color-info !default;

// Highlight Code

$highlight-background: $light-gray;
$highlight-scroll: hsla(0, 0%, 100%, 0.5);

// Buttons

$try-button-background: $brandmaker-orange !default;
$try-button-unauth-text: $mako-gray !default;
$try-button-unauth-background: $white-gray !default;

$btn-background-color: transparent !default;
$btn-border-color: $gray-400 !default;
$btn-font-color: inherit !default;
$btn-box-shadow-color: $black !default;

$btn-authorize-background-color: $brandmaker-orange !default;
$btn-authorize-border-color: $brandmaker-orange !default;
