.info {
    &-wrapper {
        display: flex;
        align-items: center;
        z-index: 2;
        padding-left: 10px;
        position: relative;
    }

    &-inner {
        position: absolute;
        background-color: $white !important;
        padding: 10px;
        right: 100%;
        top: -50%;
        width: 18vw;
        max-width: 20rem;
        border-radius: 4px;
        box-shadow: 0 0 6px 1px rgba(0, 0, 0, 0.75);

        .header {
            margin: 0;
            h1 {
                @include text_headline();
                text-transform: none;
                font-size: large;
                color: $info-header-color;
                margin: 0 0 5px 0;
            }
        }
        .body {
            margin: 0 0 0 15px;

            &-inner {
                margin: 10px 0;
                h1 {
                    @include text_body();
                    margin: 0;
                    font-size: medium;
                    color: $info-body-color;
                    font-weight: 50;
                }
                .notice {
                    font-size: small;
                    color: rgb(153, 152, 152);
                    margin-left: -15px;
                }

                &.link {
                    margin-left: -15px;
                    margin-top: 10px;
                    h1 {
                        @include text_headline();
                        text-transform: none;
                        font-size: 105%;
                        color: $info-body-color;
                    }
                    a {
                        @include text_headline();
                        text-transform: none;
                        font-size: 105%;
                        color: $info-link-color;
                    }
                }
                > ul {
                    margin: 0;
                    margin-left: 15px;
                    padding-left: 0px;
                    > li {
                        @include text_body();
                        margin: 0;
                        font-size: medium;
                        color: $info-body-color;
                        font-weight: 50;

                        span {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}
