table {
    width: 100%;
    padding: 0 10px;

    border-collapse: collapse;

    &.model {
        tbody {
            tr {
                td {
                    padding: 0;

                    vertical-align: top;

                    &:first-of-type {
                        width: 174px;
                        padding: 0 0 0 2em;
                    }
                }
            }
        }
    }

    &.headers {
        td {
            font-size: 12px;
            font-weight: 300;

            vertical-align: middle;

            @include text_code();
        }

        .header-example {
            color: #999;
            font-style: italic;
        }
    }

    tbody {
        tr {
            &:first-child {
                td {
                    padding-top: 12px !important;
                }
            }

            td {
                padding: 6px 12px 6px 3px;

                vertical-align: top;

                > input {
                    background-color: $bg-medium !important;
                    border: thin solid hsla(0, 0%, 100%, 0.5) !important;
                    color: #fff !important;
                    margin: 0 !important;

                    &.invalid {
                        animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
                        color: red !important;
                        border: thin solid red !important;
                    }

                    &:focus {
                        border: thin solid white !important;
                        outline: none !important;
                    }
                }
            }
        }
    }

    thead {
        tr {
            th,
            td {
                font-size: 12px;
                font-weight: 600;

                padding: 6px 3px;

                text-align: left;

                border-bottom: 1px solid rgba($table-thead-td-border-bottom-color, 0.5);

                @include text_body();
            }
        }
    }
}

td {
    .markdown,
    .renderedMarkdown {
        > p {
            margin: 0 !important;
        }
    }
}

.parameters-col_description {
    width: 99%; // forces other columns to shrink to their content widths
    margin-bottom: 2em;
    input[type="text"] {
        width: 100%;
        max-width: 340px;
    }

    select {
        border-width: 1px;
    }
}

.parameters-col_name {
    min-width: 7.5rem;
}

.parameters-col_data_type {
    min-width: 10rem;
}
.parameters-col_data-type {
    .markdown > p, .renderedMarkdown > p {
        font-family: monospace;
    }
}

.parameter__name {
    font-size: 16px;
    font-weight: normal;

    // hack to give breathing room to the name column
    // TODO: refactor all of this to flexbox
    margin-right: 0.75em;

    @include text_headline();

    &.required {
        color: $table-parameter-name-required-font-color;

        &:after {
            font-size: 10px;

            position: relative;
            top: -5px;

            padding: 2px;

            content: "required";

            color: rgba($table-parameter-name-required-font-color, 0.6);
        }
    }
}

.parameter__default {
    font-size: small;
    color: $prop-format-font-color;
    > p {
        margin: 0 !important;
    }
}

.parameter__in,
.parameter__extension {
    font-size: 12px;
    font-style: italic;

    @include text_code($table-parameter-in-font-color);
}

.parameter__deprecated {
    font-size: 12px;
    font-style: italic;

    @include text_code($table-parameter-deprecated-font-color);
}

.parameter__empty_value_toggle {
    display: block;
    font-size: 13px;
    padding-top: 5px;
    padding-bottom: 12px;

    input {
        margin-right: 7px;
    }

    &.disabled {
        opacity: 0.7;
    }
}

.table-container {
    padding: 0;
    margin-bottom: 20px;
}

.response-col_description {
    width: 99%; // forces other columns to shrink to their content widths
}

.response-col_links {
    min-width: 6em;
}

.response__extension {
    font-size: 12px;
    font-style: italic;

    @include text_code($table-parameter-in-font-color);
}
