.auth-btn-wrapper {
    display: flex;

    padding: 10px 0;

    .btn-done {
        margin-right: 1em;
    }
}

.auth-label {
    margin: 5px 0 1px 0;
}

.auth-input {
    background-color: $bg-lightest !important;
    border: thin solid hsla(0, 0%, 100%, 0.5) !important;
    color: #fff !important;

    &.error {
        animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
        color: red !important;
        border: thin solid red !important;
    }

    &:focus {
        border: thin solid white !important;
        outline: none !important;
    }
}

.auth-wrapper {
    display: flex;
    flex-direction: column;

    flex: 1;
    justify-content: flex-end;

    .authorize {
        padding-right: 20px;
        margin-right: 10px;
    }
}

.auth {
    &-header {
        display: flex;
        align-items: center;
        width: 100%;
        padding-left: 5px;

        h3 {
            font-size: medium;
            font-weight: 100;
            margin: 0.5em 0;
        }
    }

    &-content {
        padding-left: 10px;
        width: 100%;

        > .auth-content-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &.closed {
            display: none;
        }
    }
}

.inner-auth-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    
    > code {
        font-size: 15px;
        font-weight: 100;
    }
}

.auth-container {
    margin: 0 0 10px 0;
    border-bottom: 1px solid $auth-container-border-color;

    &:last-of-type {
        margin: 0;
        border: 0;
    }

    h4 {
        margin: 5px 0 15px 0 !important;
    }

    .wrapper {
        margin: 0;
        padding: 0;
    }

    input[type="text"],
    input[type="password"] {
        min-width: 230px;
    }

    .errors {
        font-size: 12px;

        padding: 10px;

        border-radius: 4px;

        background-color: #ffeeee;

        color: red;

        margin: 1em;

        @include text_code();

        b {
            text-transform: capitalize;
            margin-right: 1em;
        }
    }
}

.scopes {
    h2 {
        font-size: 14px;

        @include text_headline();

        a {
            font-size: 12px;
            color: $auth-select-all-none-link-font-color;
            cursor: pointer;
            padding-left: 10px;
            text-decoration: underline;
        }
    }
}

.scope-def {
    padding: 0 0 20px 0;
}
