@mixin response-codes($color) {
    background-color: rgba($color, 1);
    padding: 3px 10px;
    width: fit-content;
    border-radius: 3px;
    height: fit-content;

    h1 {
        color: white;
        font-size: 13px;
        font-weight: normal;
        margin: 0;
        padding: 0;
    }
}

.response-code {
    @include text_body();

    &.informational {
        @include response-codes($_color-informational);
    }

    &.successful {
        @include response-codes($_color-successful);
    }

    &.redirects {
        @include response-codes($_color-redirects);
    }

    &.client-error {
        @include response-codes($_color-client-error);
    }

    &.server-error {
        @include response-codes($_color-server-error);
    }

    &.not-defined {
        @include response-codes($_color-not-defined);
    }
}

.response-example-schema-name {
    font-size: medium;
    font-weight: 400;
    margin: 0 0 10px 0;

    > .response-schema {
        font-family: monospace;
    }
}

.live-response-inner {
    .live-response-inner-section:not(:first-of-type) {
        .live-response-inner-section-header {
            h4 {
                margin: 20px 0 10px 0;
            }
        }
    }
    .live-response-inner-section:first-of-type {
        .live-response-inner-section-header {
            h4 {
                margin: 10px 0 10px 0;
            }
        }
    }
}

.live-response-table-inner {
    .live-response-table-inner-header {
        margin: 20px 0 10px 0;
        font-weight: 100;
    }
}
.live-response-table-inner:first-of-type {
    .live-response-table-inner-header {
        margin: 0 0 10px 0;
    }
}