.highlight-code {
    position: relative;
    background: $bg-lighter;
    border-radius: 5px;
    color: #ccc;
    margin: 0;
    padding: 10px 5px 10px 10px;
    position: relative;

    font-size: 0.85em !important;

    > .microlight {
        overflow-y: auto;
        max-height: 25vh;
        min-height: 6em;

        code {
            white-space: pre-wrap !important;
            word-break: break-all;
        }
    }
}

.highlight-button {
    color: $dialog-ux-modal-content-font-color;
    margin: 3px;
    padding: 3px;
    border-radius: 3.5px;
    background-color: $transparent;
    border-radius: 4px;

    &-wrapper {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        flex-direction: row;
        margin: 0;
        align-items: center;
        margin-right: 15px;
        background: $bg-lighter;
        padding: 1px;
        border-radius: 5px;
    }

    &:hover {
        background-color: rgba($color: white, $alpha: 0.1);
    }
}

.highlight-hover {
    color: rgba($color: white, $alpha: 0.2);
    font-size: small;
    background-color: transparent;
    font-family: Calibri, sans-serif;
    padding-right: 5px;
    margin: 0;
    cursor: default;
}

.highlight-code-inner {
    background: transparent;
    color: #cccccc;
    max-height: 25vh;
    overflow: auto;
    overflow-x: auto;
    font-family: Consolas, monaco, monospace;

    .headerline {
        font-family: Consolas, monaco, monospace;
    }
    .headerline:last-child {
        margin-bottom: 10px;
    }

    scrollbar-color: $highlight-scroll transparent !important;
    scrollbar-width: thin;
}

.big {
    padding: 20px;
    max-height: 90vh;
    max-width: 75vw;
    border-radius: 7.5px;
}

.highlight-code-inner::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
}
.highlight-code-inner::-webkit-scrollbar-track {
    background-color: none;
}
.highlight-code-inner::-webkit-scrollbar-thumb {
    background-color: $highlight-scroll;
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
}

.hljs {
    display: flex;
    flex-direction: row;
    code {
        display: flex;
        flex-direction: column;
    }

    margin: 0 0 10px 0;
    &-strong {
        font-weight: bold;
    }
    &-emphasis {
        font-family: italic;
    }
    &-selector-tag {
        color: #cc99cc;
    }
    &-keyword {
        color: #cc99cc;
    }
    &-section {
        color: #6699cc;
    }
    &-title {
        color: #6699cc;
    }
    &-addition {
        color: #99cc99;
    }
    &-bullet {
        color: #99cc99;
    }
    &-symbol {
        color: #99cc99;
    }
    &-string {
        color: #6a6a6a;
    }
    &-attribute {
        color: #ffcc66;
    }
    &-link {
        color: #f99157;
    }
    &-meta {
        color: #f99157;
    }
    &-params {
        color: #f99157;
    }
    &-type {
        color: #f99157;
    }
    &-literal {
        color: #f99157;
    }
    &-builtin-name {
        color: #f99157;
    }
    &-built_in {
        color: #f99157;
    }
    &-number {
        color: #f99157;
    }
    &-deletion {
        color: #f2777a;
    }
    &-regexp {
        color: #f2777a;
    }
    &-selector-class {
        color: #f2777a;
    }
    &-selector-id {
        color: #f2777a;
    }
    &-name {
        color: #ffffff;
    }
    &-tag {
        color: #ffffff;
    }
    &-template-variable {
        color: #ffffff;
    }
    &-variable {
        color: #f2777a;
    }
    &-quote {
        color: #999999;
    }
    &-comment {
        color: #999999;
    }
}
