.drawer {
    position: sticky;
    top: 75px;
    width: 30vw;
    max-width: 30rem;
    height: calc(100vh - 75px);
    overflow: auto;
    background-color: $bg-light;
    padding: 1em 0.5em;

    scrollbar-color: $navigation-scroll transparent !important;
    scrollbar-width: thin;

    &::-webkit-scrollbar {
        width: 20px;
        height: 20px;
    }

    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 20px 20px transparent;
        border: solid 6px transparent;
    }

    &::-webkit-scrollbar-thumb {
        box-shadow: inset 0 0 20px 20px $navigation-scroll;
        border: solid 6px transparent;
        border-radius: 20px;
    }
    /* set button(top and bottom of the scrollbar) */
    &::-webkit-scrollbar-button {
        display: none;
    }

    &::-webkit-scrollbar-corner {
        background-color: transparent;
    }

    &-section {
        
        padding-top: 0.5em;
        padding-bottom: 1em;
        color: $white;
        cursor: default;
        width: 100%;

        &-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            border-radius: 1mm;
            padding: 0 0.5em;
        }
        &-header:hover{
            background-color: rgba(255, 255, 255, 0.03);
        }
        &-header.nav:hover{
            background-color: rgba(255, 255, 255, 0.0) !important;
        }
        &-header.not-supported-auth {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }
        &-content {
            margin-left: 15px;
            &.closed {
                display: none;
            }
        }
        &-content.nav {
            margin-left: 0!important;
        }
        &-content-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            > Input {
                width: 100%;
            }
        }

        &.add-auth {
            padding-bottom: 0 !important;
        }
        &.auth-upper {
            padding-top: 0 !important;
        }
    }
    &-headline {
        font-size: medium;
        text-transform: uppercase;
    }
    &-section:not(:last-of-type) {
        border-bottom: 1px solid $bg-lighter-blue;
    }
}

.arrow {
    margin-left: 10px;
}

.auth-item {
    color: $navigation-auth-item;
    background: $bg-light;
    margin: 0 10px 0 0;
}
