.topbar
{
    &.title
    {
        text-align: center;
        color: white;
        font-size: xx-large;
    }
    &.description
    {
        text-align: left;
        color: white;
        font-size: 1.25rem;
        font-weight: lighter;
        display: flex;
        justify-content: flex-start;
        padding: 1rem;
        margin-left: 2rem;
        align-self: flex-start;
    }

    &-stick-top
    {
        position: sticky;
        top: 75px;
        height: 0px;
        width: 100%;
        border-bottom: 1px solid $bg-lightest;
        background: rgba(14,19,60,1);
        z-index: 10;
    }
}

.topbar-wrapper
{
    display: flex;
    width: 100%;
    align-items: center;
    background-color: transparent;
    flex-direction: column;
    transform: scale(1);
}