.nav-tag {
    &-wrapper {
        &.selected {
            > .nav-tag-content {
                display: block;
            }

            > .nav-tag-header {
                font-weight: 550;
            }
        }
        > .nav-tag-content {
            display: none;
        }
    }

    &-header {
        border-radius: 1mm;
        padding: 5px;
        margin: 2px 0 2px 0;
        font-size: 1.1rem;

        &:hover {
            background-color: rgba($white, 0.05);
        }
        &.open {
            font-weight: 600;
        }
    }

    &-content {
        border-left: solid thin $bg-light-blue;
        margin-left: 15px;
    }

    &.outer {
        margin-bottom: 1rem;
    }
}

.nav-op {
    &-wrapper {
        padding: 7px 10px 7px 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &.selected {
            border-left: solid 2px $bg-lighter-blue;
            padding: 7px 10px 7px 8px;
            > div {
                font-weight: 550;
            }
        }
    }
    &-content {
        display: flex;
        align-items: center;
    }
    &-deprecated {
        font-size: small;
        padding-right: 5px;
        font-style: italic;
        color: $_color-disabled;
    }

    &-wrapper:hover {
        background-color: rgba(255, 255, 255, 0.03);
    }
}

@mixin nav-method($color) {
    background-color: rgba($color, 0.5);
    border: solid rgba($color, 1) thin;

    &.selected {
        background-color: rgba($color, 1);
    }
}

.method-icon {
    border-radius: 1mm;
    padding: 0.1rem 0.5rem 0.25rem;
    margin-left: 5px;
    transition: all 0.25s ease-out;

    span {
        color: white;
        font-size: small;
    }

    &.post {
        @include nav-method($_color-post);
    }

    &.put {
        @include nav-method($_color-put);
    }

    &.delete {
        @include nav-method($_color-delete);
    }

    &.get {
        @include nav-method($_color-get);
    }

    &.patch {
        @include nav-method($_color-patch);
    }

    &.head {
        @include nav-method($_color-head);
    }

    &.options {
        @include nav-method($_color-options);
    }

    &.deprecated {
        @include nav-method($_color-disabled);
    }
}
