.mainlayout {
    display: flex;
    background-color: #0a0c23;
}

.wrapper {
    width: 100%;
    max-width: 1460px;
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.opblock-tag-section {
    display: flex;
    flex-direction: column;
}

.opblock-tag-section:not(:first-of-type) {
    .opblock-tag {
        border-top: 2px solid $opblock-tag-border-bottom-color;
    }
}

.try-out.btn-group {
    padding: 0;
    display: flex;
    flex: 0.1 2 auto;
}

.try-out__btn {
    margin-left: 1.25rem;
}

@mixin method($color) {
    .opblock-summary-method {
        background: $color;
    }

    .opblock-summary {
        border-bottom: 2px solid $color;
    }
}

.opblock-tag {
    display: flex;
    align-items: center;

    margin: 0 -20px;
    padding: 20px;

    &.models {
        padding: 0
    }

    transition: all 0.2s;

    font-size: 24px;

    @include text_headline();

    &.no-desc {
        span {
            flex: 1;
        }
    }

    svg {
        transition: all 0.4s;
    }

    small {
        font-size: 14px;
        font-weight: normal;

        flex: 1;

        padding: 0 10px;

        @include text_body();
    }
}

.parameter__type {
    font-size: 12px;

    padding: 5px 0;

    @include text_code();
}

.parameter-controls {
    margin-top: 0.75em;
}

.examples {
    &__title {
        display: block;
        font-size: 1.1em;
        font-weight: bold;
        margin-bottom: 0.75em;
    }

    &__section {
        margin-top: 1.5em;
    }
    &__section-header {
        font-weight: bold;
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
        // color: #555;
    }
}

.examples-select {
    margin-bottom: 0.75em;
    display: inline-block;
    .examples-select-element {
        width: 100%;
    }
    &__section-label {
        font-weight: bold;
        font-size: 0.9rem;
        margin-right: 0.5rem;
    }
}

.example {
    &__section {
        margin-top: 1.5em;
    }
    &__section-header {
        font-weight: bold;
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
        // color: #555;
    }
}

.view-line-link {
    position: relative;
    top: 3px;

    width: 20px;
    margin: 0 5px;

    cursor: pointer;
    transition: all 0.5s;
}

.opblock-section {
    .opblock-section-header {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border-radius: 5px;

        > .right {
            justify-self: flex-end;
        }

        padding: 8px 20px;

        min-height: 50px;

        box-shadow: 0 1px 2px rgba($opblock-isopen-section-header-box-shadow-color, 0.1);

        > label {
            font-size: 12px;
            font-weight: bold;

            display: flex;
            align-items: center;

            margin: 0;
            margin-left: 20px;

            @include text_headline();

            > span {
                padding: 0 10px 0 0;
            }
        }

        h4 {
            font-size: 14px;
            margin: 0;

            @include text_headline();

            text-transform: uppercase;
            font-size: 1.125rem;
            font-weight: 100;
        }
    }

    .opblock-section-content {
        margin-left: 40px;

        &.live-response {
            margin-left: 20px;
            margin-bottom: 20px;
            padding: 10px 10px 10px 20px;
            border-radius: 5px;
            background-color: $bg-light;
        }
    }
}

.opblock-summary {
    display: flex;
    align-items: center;
    background: $bg-medium;
    padding: 20px 20px 20px 40px;
    margin: 0 -20px;

    cursor: default;

    .view-line-link {
        position: relative;
        top: 2px;

        width: 0;
        margin: 0;

        cursor: pointer;
        transition: all 0.5s;
    }

    &.summary-fixed {
        position: sticky;
        top: 75px;
        z-index: 1;
    }

    &:hover {
        .view-line-link {
            width: 18px;
            margin: 0 5px;
        }
    }
}

.opblock {
    margin: 0 0 40px 0;

    .tab-header {
        display: flex;

        flex: 1;

        .tab-item {
            padding: 0 40px;

            &:first-of-type {
                padding: 0 40px 0 0;
            }
            &.active {
                h4 {
                    span {
                        position: relative;
                    }
                }
            }
        }
    }

    .opblock-summary-method {
        font-size: 14px;
        font-weight: bold;

        min-width: 80px;
        padding: 6px 0;

        text-align: center;

        border-radius: 3px;
        background: $opblock-summary-method-background-color;
        text-shadow: 0 1px 0 rgba($opblock-summary-method-text-shadow-color, 0.1);

        @include text_headline($opblock-summary-method-font-color);
    }

    .opblock-summary-path,
    .opblock-summary-operation-id,
    .opblock-summary-path__deprecated {
        font-size: 16px;
        @media (max-width: 768px) {
            font-size: 12px;
        }

        display: flex;
        align-items: center;

        word-break: break-word;

        padding: 0 10px;

        @include text_code();
    }

    .opblock-summary-path {
        flex-shrink: 0;
        max-width: calc(100% - 110px - 15rem);
    }

    .opblock-summary-path__deprecated {
        text-decoration: line-through;
    }

    .opblock-summary-operation-id {
        font-size: 14px;
    }

    .opblock-summary-description {
        font-size: 13px;

        flex: 1 1 auto;

        word-break: break-word;

        @include text_body();
    }

    &.opblock-post {
        @include method($_color-post);
    }

    &.opblock-put {
        @include method($_color-put);
    }

    &.opblock-delete {
        @include method($_color-delete);
    }

    &.opblock-get {
        @include method($_color-get);
    }

    &.opblock-patch {
        @include method($_color-patch);
    }

    &.opblock-head {
        @include method($_color-head);
    }

    &.opblock-options {
        @include method($_color-options);
    }

    &.opblock-deprecated {
        opacity: 0.6;

        @include method($_color-disabled);
    }

    .opblock-schemes {
        padding: 8px 20px;

        .schemes-title {
            padding: 0 10px 0 0;
        }
    }
}

.opblock-section-request-body {
    padding-bottom: 20px;
}

.filter {
    .operation-filter-input {
        width: 100%;
        margin: 20px 0;
        padding: 10px 10px;

        border: 2px solid $operational-filter-input-border-color;
    }
}

.filter,
.download-url-wrapper {
    .failed {
        color: red;
    }

    .loading {
        color: #aaa;
    }
}

.model-example {
    margin-top: 1em;
}

.tab {
    display: flex;

    padding: 0;

    list-style: none;

    li {
        font-size: 12px;

        min-width: 60px;
        padding: 0;

        cursor: pointer;

        @include text_headline();

        &:first-of-type {
            position: relative;

            padding-left: 0;
            padding-right: 12px;

            &:after {
                position: absolute;
                top: 0;
                right: 6px;

                width: 1px;
                height: 100%;

                content: "";

                background: rgba($tab-list-item-first-background-color, 0.2);
            }
        }

        &.active {
            font-weight: bold;
        }

        button.tablinks {
            background: none;
            border: 0;
            padding: 0;

            color: inherit;
            font-family: inherit;
            font-weight: inherit;
        }
    }
}

.opblock-description-wrapper {
    padding: 20px !important;
    margin: 0 0 0 20px !important;
}

.opblock-description-wrapper,
.opblock-external-docs-wrapper,
.opblock-title_normal {
    font-size: 12px;

    margin: 0 0 5px 0;
    padding: 15px 20px;

    @include text_body();

    h4 {
        font-size: 12px;

        margin: 0 0 5px 0;

        @include text_body();
    }

    p {
        font-size: 14px;

        margin: 0;

        @include text_body();
    }
}

.opblock-external-docs-wrapper {
    h4 {
        padding-left: 0px;
    }
}

.execute-wrapper {
    padding: 20px 0;
}

.body-param-options {
    display: flex;
    flex-direction: column;

    .body-param-edit {
        padding: 10px 0;
    }

    label {
        padding: 8px 0;
        select {
            margin: 3px 0 0 0;
        }
    }
}

.responses-inner {
    padding: 20px;

    h5,
    h4 {
        font-size: 12px;

        margin: 10px 0 5px 0;

        @include text_body();
    }

    .curl {
        white-space: normal;
    }
}

.response-col_status {
    font-size: 14px;

    @include text_body();

    .response-undocumented {
        font-size: 11px;

        @include text_code($response-col-status-undocumented-font-color);
    }
}

.response-col_links {
    padding-left: 2em;
    max-width: 40em;
    font-size: 14px;

    @include text_body();

    .response-undocumented {
        font-size: 11px;

        @include text_code($response-col-links-font-color);
    }

    .operation-link {
        margin-bottom: 1.5em;

        .description {
            margin-bottom: 0.5em;
        }
    }
}

.opblock-body {
    .opblock-loading-animation {
        display: block;
        margin: 3em;
        margin-left: auto;
        margin-right: auto;
    }
}

.opblock-body pre.microlight {
    font-size: .85em !important;

    margin: 0;
    padding: 10px;

    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-all;
    word-break: break-word;
    hyphens: auto;

    border-radius: 4px;
    background: $bg-lighter;

    overflow-wrap: break-word;
    @include text_code($opblock-body-font-color);

    // disabled to have syntax highliting with react-syntax-highlight
    // span
    // {
    //     color: $opblock-body-font-color !important;
    // }

    .headerline {
        display: block;
    }
}

.curl-command {
    position: relative;
}

.download-contents {
    position: absolute;
    bottom: 10px;
    right: 10px;
    cursor: pointer;
    background: #7d8293;
    text-align: center;
    padding: 5px;
    border-radius: 4px;
    font-family: sans-serif;
    font-weight: 600;
    color: white;
    font-size: 14px;
    height: 30px;
    justify-content: center;
    align-items: center;
    display: flex;
}

.scheme-container {
    margin: 0 0 20px 0;
    padding: 30px 0;

    background: $scheme-container-background-color;
    box-shadow: 0 1px 2px 0 rgba($scheme-container-box-shadow-color, 0.15);

    .schemes {
        display: flex;
        align-items: flex-end;

        > label {
            font-size: 12px;
            font-weight: bold;

            display: flex;
            flex-direction: column;

            margin: -20px 15px 0 0;

            @include text_headline();

            select {
                min-width: 130px;

                text-transform: uppercase;
            }
        }
    }
}

.loading-container {
    padding: 40px 0 60px;
    margin-top: 1em;
    min-height: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .loading {
        position: relative;

        &:after {
            font-size: 10px;
            font-weight: bold;

            position: absolute;
            top: 50%;
            left: 50%;

            content: "loading";
            transform: translate(-50%, -50%);
            text-transform: uppercase;

            @include text_headline();
        }

        &:before {
            position: absolute;
            top: 50%;
            left: 50%;

            display: block;

            width: 60px;
            height: 60px;
            margin: -30px -30px;

            content: "";
            animation: rotation 1s infinite linear, opacity 0.5s;

            opacity: 1;
            border: 2px solid rgba($loading-container-before-border-color, 0.1);
            border-top-color: rgba($loading-container-before-border-top-color, 0.6);
            border-radius: 100%;

            backface-visibility: hidden;

            @keyframes rotation {
                to {
                    transform: rotate(360deg);
                }
            }
        }
    }
}

.response-controls {
    padding-top: 1em;
    display: flex;
}

.response-control-media-type {
    margin-right: 1em;

    &--accept-controller {
        select {
            border-color: $response-content-type-controls-accept-header-select-border-color;
        }
    }

    &__accept-message {
        color: $response-content-type-controls-accept-header-small-font-color;
        font-size: 0.7em;
    }

    &__title {
        display: block;
        margin-bottom: 0.2em;
        font-size: 0.7em;
    }
}

.response-control-examples {
    &__title {
        display: block;
        margin-bottom: 0.2em;
        font-size: 0.7em;
    }
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.hidden {
    display: none;
}

.no-margin {
    height: auto;
    border: none;
    margin: 0;
    padding: 0;
}

.float-right {
    float: right;
}

.svg-assets {
    position: absolute;
    width: 0;
    height: 0;
}

section {
    h3 {
        @include text_headline();
    }
}

a.nostyle {
    text-decoration: inherit;
    color: inherit;
    cursor: pointer;
    display: inline;

    &:visited {
        text-decoration: inherit;
        color: inherit;
        cursor: pointer;
    }
}

.fallback {
    padding: 1em;
    color: #aaa;
}

.version-pragma {
    height: 100%;
    padding: 5em 0px;

    &__message {
        display: flex;
        justify-content: center;
        height: 100%;
        font-size: 1.2em;
        text-align: center;
        line-height: 1.5em;

        padding: 0px 0.6em;

        > div {
            max-width: 55ch;
            flex: 1;
        }

        code {
            background-color: #dedede;
            padding: 4px 4px 2px;
            white-space: pre;
        }
    }
}

.opblock-link {
    font-weight: normal;

    &.shown {
        font-weight: bold;
    }
}

span {
    &.token-string {
        color: #555;
    }

    &.token-not-formatted {
        color: #555;
        font-weight: bold;
    }
}

.hover-header:hover {
    background-color: hsla(0, 0%, 100%, 0.04);
    cursor: pointer;
}
